<template>
	<div class="welcome">
		<div>
			<h1 class="font-55">
				Growing & Getting Better. <span>Together.</span>
			</h1>
			<div class="btn">
				<button @click="handlePopop" class="font-16">Get Started</button>
			</div>
			<Popup>
				<form @submit.prevent="addUser()">
					<div class="input">
						<label for="username">Username</label>
						<input v-model="username" id="username" type="text" required placeholder="Karl">
					</div>
					<div class="input">
						<label for="project">Project name</label>
						<input v-model="projectName" id="project" type="text" required placeholder="...learn something">
					</div>
					<div class="input">
						<label for="priority">Select project priority</label>
						<select id="priority" v-model="priority">
							<option value="0">low</option>
							<option value="1">middle</option>
							<option value="2">high</option>
							<option value="3">very high</option>
						</select>
					</div>
					<div class="input">
						<label for="comment">Add project comment</label>
						<textarea v-model="projectComment" id="comment" placeholder="Lorem ipsum dolor sit amet, consectetur adipisicing elit,"></textarea>
					</div>
					<button class="font-16">Add</button>
				</form>
			</Popup>
		</div>
	</div>
</template>

<script>

	import Popup from '@/components/popup/Popup'
	import { mapMutations } from 'vuex'

	export default {
		components: {
			Popup
		},
		data() {
			return {
				username: '',
				projectName: '',
				priority: 0,
				projectComment: '',
			}
		},
		methods: {
			...mapMutations([
				'handlePopop',
				'setAuthState',
			]),
			addUser() {
				this.$store.commit('addProject',{
					id: Math.random(),
					name: this.projectName,
					status: 'not started',
					priority: this.priority,
					timeSpend: 0,
					progress: 0,
					color: '',
					projectComment: [{
						text: this.projectComment
					}],
					todos: []
				})
				this.$store.commit('addUserName',this.username)
				this.$store.commit('setAuthState')
				this.$store.commit('handlePopop')
				this.$router.push({ path: '/dashboard' })
			},
		},
	}

</script>`

<style lang="scss" scoped>
	.welcome {
		min-height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	h1 {
		font-weight: 700;
		width: 70%;
		margin: auto;
		text-align: center;
		animation: fadeIn 2.5s both .2s;
		span {
			color: #3ea6ff;
			display: block;
			animation: fadeIn 2.7s both .3s;
		}
	}
	.btn {
		max-width: 220px;
		margin: auto;
		button {
			margin-top: 60px;
			font-weight: 700;
			animation: fadeIn 2.9s both .4s;
		}
	}
	form {
		display: block;
		margin: auto;
		padding: 30px;
		border-radius: 30px;
		max-width: 400px;
		background: var(--main-background);
		animation: slideUp .2s both;
		backface-visibility: hidden;
		box-shadow: 0 0 0 4px rgba(62,166,255,0.2);
		button {
			font-weight: 700;
		}
	}
	.input {
		margin-bottom: 10px;
	}
</style>