<template>
	<div class="project">
		<div class="project__header">
			<h1 class="font-34">
				{{ title }}
			</h1>
			<button @click="handlePopop" class="font-16">Add Todo</button>
		</div>
		<div class="project__description font-16">
			{{ getProject[0].projectComment[0].text }}
		</div>
		<div class="todo__container">
			<div class="todo__card" v-for="(todo, i) in getProject[0].todos.slice().reverse()" :key="i">
				<div class="todo__box">
					<div class="todo__name font-21" @click="deleteTodo(todo.projectName,todo.id)">
						{{ todo.name }}
					</div>
					<div class="todo_description" v-for="(item , j) in todo.todoComment" :key="j">
						<div v-html="item.text"></div>
					</div>
				</div>
			</div>
		</div>
		<div class="todo__filter">
			<h2 class="font-21">Filter</h2>
			Lorem ipsum, dolor sit amet consectetur adipisicing elit. Doloribus corporis voluptatibus ea officiis ipsa? Asperiores maiores unde laudantium optio molestiae numquam voluptas itaque quasi rerum atque. Eveniet sed voluptatem tempora!
			<!-- <input type="text" style="width: 100%" placeholder="search">
			<div>
				<label for="status">in progress</label> <input name="status" type="radio">		
			</div>
			<div>
				<label for="status">hold</label> <input name="status" type="radio">		
			</div>
			<div>
				<label for="status">done</label> <input name="status" type="radio">		
			</div> -->
		</div>
		<Popup>
			<form @submit.prevent="addTodo()">
				<div class="input">
					<label for="project">Todo name</label>
					<input v-model="todoName" id="project" type="text" required placeholder="learn something...">
				</div>
				<div class="input">
					<label for="priority">Select todo priority</label>
					<select id="priority" v-model="priority">
						<option value="0">low</option>
						<option value="1">middle</option>
						<option value="2">high</option>
						<option value="3">very high</option>
					</select>
				</div>
				<div class="input">
					<label for="comment">Add todo comment</label>
					<textarea v-model="todoComment" id="comment" placeholder="Lorem ipsum dolor sit amet, consectetur adipisicing elit,"></textarea>
				</div>
				<button class="font-16">Add</button>
			</form>
		</Popup>
	</div>
</template>

<script>

	import Popup from '@/components/popup/Popup'
	import { mapState , mapMutations } from 'vuex'

	export default {
		components: {
			Popup
		},
		data() {
			return {
				title: this.$route.params.name,
				todoName: '',
				priority: 0,
				todoComment: '',
			}
		},
		methods: {
			...mapMutations([
				'handlePopop'
			]),
			addTodo() {
				this.$store.state.userData.projects.filter(el => {
					if(el.name == this.$route.params.name) {
						el.todos.push({
							projectName: el.name,
							id: Math.random(),
							name: this.todoName,
							status: 'not started',
							priority: this.priority,
							timeSpend: 0,
							progress: 0,
							color: '',
							todoComment: [{
								text: this.todoComment
							}],
							subTasks: []
						})
					}
				})
				this.$store.commit('handlePopop')
				this.$store.commit('updateUserData',{
					name: this.$store.state.userData.name ,
					projects: this.$store.state.userData.projects
				})
				this.todoName = ''
				this.priority = 0
				this.todoComment = ''
			},
			deleteTodo(name,id) {
				this.$store.state.userData.projects.filter(el => {
					if (el.name == name) {
						el.todos.filter((todo,index) => {
							if (todo.id == id) {
								console.log(el.todos[index])
								el.todos.splice(index,1)
							}
						})
					}
				})
				this.$store.commit('updateUserData',{
					name: this.$store.state.userData.name ,
					projects: this.$store.state.userData.projects
				})
			}
		},
		computed: {
			getProject() {
				return this.$store.state.userData.projects.filter(el => {
					return el.name == this.$route.params.name
				}).reverse()
			},
		}
	}

</script>

