import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
state: {
		authState: false,
		popupOpened: false,
		userData: {
			name: '',
			projects: [],
		},
	},
	mutations: {
		setAuthState: (state) => state.authState = true ,
		setUserData: (state, payload) => state.userData = payload ,
		handlePopop: (state) => state.popupOpened = !state.popupOpened ,
		addProject: (state, payload) => state.userData.projects.push(payload) ,
		addUserName: (state, payload) => {
			state.userData.name = payload
			localStorage.setItem('userData',JSON.stringify(state.userData))
		},
		updateUserData: (state, payload) => {
			state.userData = payload
			localStorage.setItem('userData',JSON.stringify(state.userData))
		},
	},
	actions: {
		checkAuthState({ commit }){
			if (localStorage.getItem('userData') !== null) {
				commit('setAuthState');
			}
		},
		getUserData({ state , commit }){
			if (localStorage.getItem('userData') !== null) {
				console.table(JSON.parse(localStorage.getItem('userData')).projects)
				commit('setUserData',JSON.parse(localStorage.getItem('userData')));
			}
		}
	},
	modules: {
	}
})
