<template>
	<div class="popup" v-if="popupOpened">
		<div class="overlay" @click="handlePopop"></div>
		<div class="close-btn"></div>
		<div class="popup__container">
			<slot/>
		</div>
	</div>
</template>

<script>

	import { mapState , mapMutations } from 'vuex'

	export default {
		methods: {
			...mapMutations([
				'handlePopop'
			])
		},
		computed: {
			...mapState([
				'popupOpened'
			])
		}
	}

</script>

<style lang="scss" scoped>
	.popup {
		width: 100%;
		height: 100%;
		overflow: auto;
		display: flex;
		align-items: center;
		justify-content: center;
		position: fixed;
		top: 0;
		left: 0;
		opacity: 1;
		z-index: 9999999999;
		&__container {
			margin: auto;
			padding: 30px 15px;
			position: relative;
		}
		.overlay {
			position: absolute;
			background: var(--main-background);
			opacity: 0.95;
			width: 100%;
			height: 100vh;
			top: 0;
			left: 0;
		}
	}
</style>