<template>
	<div class="projects">
		<div class="projects__header">
			<h1 class="font-34">Projects</h1>
			<button @click="handlePopop" class="font-16">Add</button>
		</div>
		<div class="projects__container">
			<div class="projects__card" v-for="(project , i) in userData.projects.slice().reverse()" :key="i">
				<div class="projects__box">
					<router-link class="font-21 projects__name" :to="`/projects/${project.name}`">
						{{project.name}}
					</router-link>
					<div class="projects__description">
						<div v-for="(comment , j) in project.projectComment" :key="j">
						</div>
					</div>
				</div>
				<div class="projects__line"></div>
			</div>
		</div>
		<Popup>
			<form @submit.prevent="addProject()">
				<div class="input">
					<label for="project">Project name</label>
					<input v-model="projectName" id="project" type="text" required placeholder="learn something...">
				</div>
				<div class="input">
					<label for="priority">Select project priority</label>
					<select id="priority" v-model="priority">
						<option value="0">low</option>
						<option value="1">middle</option>
						<option value="2">high</option>
						<option value="3">very high</option>
					</select>
				</div>
				<div class="input">
					<label for="comment">Add project comment</label>
					<textarea v-model="projectComment" id="comment" placeholder="Lorem ipsum dolor sit amet, consectetur adipisicing elit,"></textarea>
				</div>
				<button class="font-16">Add</button>
			</form>
		</Popup>
	</div>
</template>

<script>

	import Popup from '@/components/popup/Popup'
	import { mapState, mapMutations } from 'vuex'

	export default {
		components: {
			Popup
		},
		data() {
			return {
				name: 'Projects',
				projectName: '',
				priority: 0,
				projectComment: '',
			}
		},
		methods: {
			...mapMutations([
				'handlePopop'
			]),
			addProject() {
				this.$store.commit('addProject',{
					id: Math.random(),
					name: this.projectName,
					status: 'not started',
					priority: this.priority,
					timeSpend: 0,
					progress: 0,
					color: '',
					projectComment: [{
						text: this.projectComment
					}],
					todos: []
				})
				this.$store.commit('handlePopop')
				this.$store.commit('updateUserData',{
					name: this.userData.name ,
					projects: this.userData.projects
				})

			},
			getName() {
			},
		},
		computed: {
			...mapState([
				'userData'
			])
		},
		beforeDestroy() {
			this.getName()
		}
	}

</script>

